

/* Fondo con círculos flotantes y desenfocados */
.background-circles {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: -1;
  pointer-events: none;
}

/* Estilo base para cada círculo */
.background-circles .circle {
  position: absolute;
  border-radius: 50%;
  background: radial-gradient(circle, rgb(213, 233, 255), transparent 70%);
  filter: blur(60px);
  animation-name: float;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-timing-function: ease-in-out;
}

/* Círculos con tamaños y posiciones variables */
.background-circles .circle1 {
  width: 100px;
  height: 100px;
  top: 10%;
  left: 15%;
  animation-duration: 18s;
  animation-delay: 0s;
}

.background-circles .circle2 {
  width: 40px;
  height: 40px;
  top: 30%;
  right: 15%;
  animation-duration: 16s;
  animation-delay: 3s;
}

.background-circles .circle3 {
  width: 35px;
  height: 350px;
  top: 50%;
  left: 30%;
  animation-duration: 17s;
  animation-delay: 1s;
}

.background-circles .circle4 {
  width: 90px;
  height: 80px;
  bottom: 15%;
  right: 25%;
  animation-duration: 15s;
  animation-delay: 2s;
}

.background-circles .circle5 {
  width: 20px;
  height: 20px;
  bottom: 10%;
  left: 15%;
  animation-duration: 14s;
  animation-delay: 4s;
}

.background-circles .circle6 {
  width: 15px;
  height: 15px;
  top: 60%;
  right: 40%;
  animation-duration: 19s;
  animation-delay: 0.5s;
}

.background-circles .circle7 {
  width: 100px;
  height: 100px;
  top: 80%;
  left: 50%;
  animation-duration: 13s;
  animation-delay: 1.5s;
}

/* Animación flotante */
@keyframes float {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(30px, -30px);
  }
}

/* Capa transparente con desenfoque para efecto glassmorphism */
.blur-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.103);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  z-index: 0;
  border-radius: 0.5rem;
}

/* Estilos header para que la capa se posicione bien */
header.position-relative {
  position: relative;
  height: 300px;
  width: 100%;
}

/* Texto del header */
.texto-header {
  position: relative;
  background: rgba(141, 141, 141, 0.001);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-radius: 12px;
  padding: 1.5rem 2rem;
  color: white;
}

/* Títulos header */
.texto-xxl {
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 1rem;
}

.fw-normal {
  font-weight: normal;
  font-size: 1rem;
  margin-top: 1rem;
}

/* Para que la app-wrapper tenga relative y controle z-index */
.app-wrapper.position-relative {
  position: relative;
  z-index: 1;
}

.app-wrapper {
  background-color: #000;
  position: relative;
  min-height: 100vh;
  overflow: hidden;
}




.background-gradient {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  pointer-events: none;
  background: 
              radial-gradient(circle at 100% 150%, #919191, transparent 50%),
              radial-gradient(circle at 50% 50%, #0e0e0e, transparent 50%),
              radial-gradient(circle at 50% 20%, #7c7c7c, transparent 50%);
  animation: moveGradient 20s infinite alternate ease-in-out;
  background-size: 200% 200%;
  filter: blur(80px); /* Si querés efecto de profundidad */
}

@keyframes moveGradient {
  0% {
    background-position: 0% 0%;
  }
  100% {
    background-position: 100% 100%;
  }
}




